import { Typography, Stack, Avatar, IconButton, styled, Divider, TextField, Button } from '@mui/material'
import ArrowForwardIosSharpIcon from '@mui/icons-material/ArrowForwardIosSharp';
import ReplyIcon from '@mui/icons-material/Reply';

import MuiAccordion from '@mui/material/Accordion';
import MuiAccordionSummary from '@mui/material/AccordionSummary';
import MuiAccordionDetails from '@mui/material/AccordionDetails';
import SubdirectoryArrowRightIcon from '@mui/icons-material/SubdirectoryArrowRight';

import { useState } from 'react'

const Accordion = styled((props) => (
  <MuiAccordion disableGutters elevation={0} square {...props} />
))(({ theme }) => ({
  border: `1px solid ${theme.palette.divider}`,
  '&:not(:last-child)': {
    borderBottom: 0,
  },
  '&:before': {
    display: 'none',
  },
}));

const AccordionSummary = styled((props) => (
  <MuiAccordionSummary
    expandIcon={(props.reply > -1)?<ArrowForwardIosSharpIcon sx={{ fontSize: '0.9rem' }} />:null}
    {...props}
  />
))(({ theme }) => ({
  backgroundColor:
    theme.palette.mode === 'dark'
      ? 'rgba(255, 255, 255, .05)'
      : 'rgba(0, 0, 0, .03)',
  flexDirection: 'row-reverse',
  '& .MuiAccordionSummary-expandIconWrapper.Mui-expanded': {
    transform: 'rotate(90deg)',
  },
  '& .MuiAccordionSummary-content': {
    marginLeft: theme.spacing(1),
  },
}));

const AccordionDetails = styled(MuiAccordionDetails)(({ theme }) => ({
  padding: theme.spacing(0),
  paddingLeft: theme.spacing(4),
  paddingRight: theme.spacing(1),
  paddingBottom: theme.spacing(1),
  borderTop: '1px solid rgba(0, 0, 0, .125)',
}));

export const Comment = (props) => {
  const [btnDisabled, setBtnDisabled] = useState(true)
  return (
    <Accordion>
      <AccordionSummary reply={props.reply}
        aria-controls="panel1bh-content"
        id="panel1bh-header">
        <Stack direction="row" justifyContent="flex-start" alignItems="center" spacing={3} marginLeft={3}>
          {props.reply < 0?<SubdirectoryArrowRightIcon sx={{ fontSize: '0.9rem', }}/>:null}
          <Avatar/>
          <Stack direction="column" justifyContent="flex-start" alignItems="flex-start" spacing={0}>
            <Typography>닉네임 (날짜)</Typography>
            <Typography>: 댓글 내용</Typography>
          </Stack>
          <IconButton>
            <ReplyIcon/>
          </IconButton>
          <div style={{flex: '1 0 0'}} />
          <Typography>
            {props.reply>0?'( +'+props.reply+' )':''}
          </Typography>
        </Stack>
      </AccordionSummary>
      <AccordionDetails>
        <Stack direction="row" divider={<Divider orientation="vertical" flexItem />} spacing={1} margin={1} marginLeft={0}>
          <TextField multiline sx={{width:'100%'}} id="new_comment_textfield" label="Add a reply" variant="outlined"
                     onChange={(e)=>{
                     if(e.target.value.length>0) setBtnDisabled(false)
                     else setBtnDisabled(true)
                     }}/>
          <Button id='new_comment_register' variant="contained" disabled={btnDisabled}>
            Register
          </Button>
        </Stack>
        {props.reply===0?'( 0 Result found )':''}
        {props.details}
        <Divider/>
      </AccordionDetails>
    </Accordion>
  )
}
