import * as React from 'react'
import PropTypes from 'prop-types'
import { useState, useEffect } from 'react'
import { useTheme, styled, alpha } from '@mui/material/styles'
import { Box, IconButton, Stack, Grow,
         Table, TableHead, TableBody, TableCell, TableFooter, TablePagination, TableRow, Slide, } from '@mui/material'
import { tableCellClasses } from '@mui/material/TableCell';
import FirstPageIcon from '@mui/icons-material/FirstPage'
import KeyboardArrowLeft from '@mui/icons-material/KeyboardArrowLeft'
import KeyboardArrowRight from '@mui/icons-material/KeyboardArrowRight'
import LastPageIcon from '@mui/icons-material/LastPage'

import { MyDialog } from './DialogContent'
import { newsData } from './stub'

function TablePaginationActions(props) {
  const theme = useTheme();
  const { count, page, rowsPerPage, onPageChange } = props;

  const handleFirstPageButtonClick = (event) => { onPageChange(event, 0) }
  const handleBackButtonClick = (event) => { onPageChange(event, page - 1) }
  const handleNextButtonClick = (event) => { onPageChange(event, page + 1) }
  const handleLastPageButtonClick = (event) => {
    onPageChange(event, Math.max(0, Math.ceil(count / rowsPerPage) - 1))
  }

  return (
    <Box sx={{ flexShrink: 0, ml: 2.5 }}>
      <IconButton onClick={handleFirstPageButtonClick} disabled={page === 0} aria-label="first page">
        {theme.direction === 'rtl' ? <LastPageIcon /> : <FirstPageIcon />}
      </IconButton>
      <IconButton onClick={handleBackButtonClick} disabled={page === 0} aria-label="previous page">
        {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
      </IconButton>
      <IconButton onClick={handleNextButtonClick} disabled={page >= Math.ceil(count / rowsPerPage) - 1} aria-label="next page">
        {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
      </IconButton>
      <IconButton onClick={handleLastPageButtonClick} disabled={page >= Math.ceil(count / rowsPerPage) - 1} aria-label="last page">
        {theme.direction === 'rtl' ? <FirstPageIcon /> : <LastPageIcon />}
      </IconButton>
    </Box>
  )
}

TablePaginationActions.propTypes = {
  count: PropTypes.number.isRequired,
  onPageChange: PropTypes.func.isRequired,
  page: PropTypes.number.isRequired,
  rowsPerPage: PropTypes.number.isRequired,
}

// function createData(post_title, post_author, post_date) {
//   return { title: post_title, author: post_author, date: post_date }
// }

export function News(props) {
  const [rows, setRows] = useState([])
  useEffect(()=>{
    setRows(newsData)
  },[])

  const isDarkMode = props.isDarkMode
  const [page, setPage] = useState(0)
  const [rowsPerPage, setRowsPerPage] = useState(10)
  // Avoid a layout jump when reaching the last page with empty rows.
  const emptyRows = page > 0 ? Math.max(0, (1 + page) * rowsPerPage - rows.length) : 0
  const handleChangePage = (event, newPage) => { setPage(newPage) }
  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10))
    setPage(0)
  }

  const [commentOpen, setCommentOpen] = useState(false)

  const [open, setOpen] = useState(false)
  const handleCommentClose = () => { setCommentOpen(false) }
  const handleCommentToggle = () => { setCommentOpen(!commentOpen) }
  const handleClose = () => { setOpen(false); setCommentOpen(false) }
  const handleToggle = () => { setOpen(!open) }

  const StyledTableCell = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
      backgroundColor: theme.palette.common.black,
      color: theme.palette.common.white,
    },
    [`&.${tableCellClasses.body}`]: {
      fontSize: 14,
    },
  }))
  
  const StyledTableRow = styled(TableRow)(({ theme }) => ({
    '&:nth-of-type(odd)': { backgroundColor: theme.palette.action.hover, },
    '&:last-child td, &:last-child th': { border: 0, }, // hide last border
    '&:hover': {
      backgroundColor:
      (isDarkMode?alpha(theme.palette.common.white, 0.15):alpha(theme.palette.common.black, 0.15)),
    },
  }))

  return (
    <div>
      <Stack marginTop={3} justifyContent={'center'} alignItems={'center'}>
        <Grow in={true} timeout={500}>
          <Table sx={{ minWidth: 500, width: '80%' }} aria-label="custom pagination table">
            <TableHead>
              <TableRow>
                <StyledTableCell style={{ width: 160 }} align="center">Number</StyledTableCell >
                <StyledTableCell component="th" scope="row">Title</StyledTableCell >
                <StyledTableCell style={{ width: 160 }} align="right">Author</StyledTableCell >
                <StyledTableCell style={{ width: 160 }} align="center">Date</StyledTableCell >
              </TableRow>
            </TableHead>
            <TableBody>
              {(rowsPerPage > 0 ? rows.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage) : rows).map((row, index) => (
                <StyledTableRow key={row.title} onClick={handleToggle}>
                  <StyledTableCell style={{ width: 160 }} align="center">{index+1}</StyledTableCell >
                  <StyledTableCell component="th" scope="row">{row.title}</StyledTableCell >
                  <StyledTableCell style={{ width: 160 }} align="right">{row.author}</StyledTableCell >
                  <StyledTableCell style={{ width: 160 }} align="center">{row.date}</StyledTableCell >
                </StyledTableRow>
              ))}
              {emptyRows > 0 && (
                <StyledTableRow style={{ height: 53 * emptyRows }}>
                  <StyledTableCell colSpan={7} />
                </StyledTableRow>
              )}
            </TableBody>
            <TableFooter>
              <Slide in={true} direction="right">
                <TableRow>
                  <TablePagination
                    rowsPerPageOptions={[10, 15, 20]}
                    colSpan={4} count={rows.length}
                    rowsPerPage={rowsPerPage} page={page}
                    SelectProps={{
                      inputProps: { 'aria-label': 'rows per page', },
                      native: true,
                    }}
                    onPageChange={handleChangePage}
                    onRowsPerPageChange={handleChangeRowsPerPage}
                    ActionsComponent={TablePaginationActions}
                  />
                </TableRow>
              </Slide>
            </TableFooter>
          </Table>
        </Grow>   
      </Stack>
      <MyDialog handleClose={handleClose} open={open}
                handleCommentClose={handleCommentClose} handleCommentToggle={handleCommentToggle} commentOpen={commentOpen}/>
    </div>
  )
}