import { Dialog, DialogTitle, DialogContent, DialogContentText, DialogActions, Box,
        TextField, IconButton, Checkbox, Button, Stack, Divider } from '@mui/material'
import { useEffect, useState } from 'react'

import CommentIcon from '@mui/icons-material/Comment'
import FavoriteBorder from '@mui/icons-material/FavoriteBorder'
import Favorite from '@mui/icons-material/Favorite'
import CloseIcon from '@mui/icons-material/Close'

import { Comment } from './Comment'

export const MyDialog= (props)=>{
  const [heart, setHeart] = useState(false)
  const pressHeart = () => { setHeart(!heart) }

  const [content, setContent] = useState({})
  useEffect(()=>{
    setHeart(false)
    setContent({
      content: '내용'
    })
  },[props.content])

  const [btnDisabled, setBtnDisabled] = useState(true)
  
  return (
    <Box>
      <Dialog full={true} fullWidth={true} maxWidth={'sm'}
              open={props.commentOpen} onClose={props.handleCommentClose} scroll={'paper'}>
        <DialogTitle>
          Comment
          <IconButton aria-label="close" onClick={props.handleCommentClose}
                      sx={{ position: 'absolute', right: 8, top: 8, color: (theme) => theme.palette.grey[500], }}>
            <CloseIcon />
          </IconButton>
        </DialogTitle>
        <DialogTitle>
          <Stack direction="row" divider={<Divider orientation="vertical" flexItem />} spacing={2}>
            <TextField multiline sx={{width:'100%'}} id="new_comment_textfield" label="Add a comment" variant="outlined"
                       onChange={(e)=>{
                        if(e.target.value.length>0) setBtnDisabled(false)
                        else setBtnDisabled(true)
                       }}/>
            <Button id='new_comment_register' variant="contained" disabled={btnDisabled}>
              Register
            </Button>
          </Stack>
        </DialogTitle>
        <DialogContent>
          <Comment reply={1} details={<Comment reply={-1}/>}/>
          <Comment reply={0}/>
          <Comment reply={0}/>
          <Comment reply={1} details={<Comment reply={-1}/>}/>
          <Comment reply={1} details={<Comment reply={-1}/>}/>
          <Comment reply={1} details={<Comment reply={-1}/>}/>
        </DialogContent>
      </Dialog>
      <Dialog full={true} fullWidth={true} maxWidth={'lg'}
              open={props.open} onClose={props.handleClose} scroll={'paper'}>
        <DialogTitle>
          제목
          <IconButton aria-label="close" onClick={props.handleClose}
                      sx={{ position: 'absolute', right: 8, top: 8, color: (theme) => theme.palette.grey[500], }}>
            <CloseIcon />
          </IconButton>
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            {content.content}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <div style={{flex: '1 0 0'}} />
          <IconButton aria-label="comment" onClick={props.handleCommentToggle}
                      sx={{ color: (theme) => theme.palette.grey[500], }}>
            <CommentIcon />
          </IconButton>
          <Checkbox icon={<FavoriteBorder />} checkedIcon={<Favorite />} checked={heart} onClick={pressHeart}/>
          <div style={{flex: '1 0 0'}} />
        </DialogActions>  
      </Dialog>
      
    </Box>
  )
}