import * as React from 'react'
import Box from '@mui/material/Box'
import { ImageList, ImageListItem, Zoom, Backdrop, CircularProgress, Grow } from '@mui/material'
import { useState, useEffect } from 'react'

import { itemData } from './stub'

export const Community = (props) => {
    const isDarkMode = props.isDarkMode
    const [open, setOpen] = useState(false)
    const handleClose = () => { setOpen(false) }
    const handleToggle = () => { if(open===false) setImageLoading(false); setOpen(!open) }
    useEffect(()=>{

    },[])

    const [imgLoading, setImageLoading] = useState(false)

    return (
    <div>
      <Box margin={10} marginTop={3}>
        <ImageList variant="masonry" cols={5} gap={16}>
          {itemData.map((item, index) => (
          <Zoom key={'image'+index} in={true} style={{ transitionDelay: (10+index*20).toString()+'ms', boxShadow: 2}}>
              <ImageListItem key={item.img} onClick={handleToggle}>
                  <img src={`${item.img}?w=248&fit=crop&auto=format`}
                      srcSet={`${item.img}?w=248&fit=crop&auto=format&dpr=2 2x`}
                      alt={item.title}
                      loading="lazy" />
              </ImageListItem>
            </Zoom>
          ))}
        </ImageList>
      </Box>

      <Backdrop sx={{ color: 'primary.main', bgcolor: !isDarkMode?'rgb(255,255,255,0.5)':'rgb(0,0,0,0.5)',
                      zIndex: (theme) => theme.zIndex.drawer + 1 }} open={open} onClick={handleClose}>
          <Grow in={open} timeout={500}>
            <div>
              {imgLoading===false?
              <Box sx={{ p: 2, bgcolor: 'background.default', borderRadius: 5, boxShadow: 12,
                  gridTemplateColumns: { md: '1fr 1fr' }, gap: 2,}}>
                <div>L<CircularProgress color="inherit" />ADING...</div>
              </Box>:null}
              <Box sx={{ boxShadow: 12, display:!imgLoading?'none':'block' }}>
                <img src={`${itemData[0].img}?w=248&fit=crop&auto=format`}
                            srcSet={`${itemData[0].img}?w=248&fit=crop&auto=format&dpr=2 2x`}
                            alt={itemData[0].title}
                            loading="lazy"
                            onLoad={()=>{setImageLoading(true)}} />
              </Box>
            </div>
          </Grow>
      </Backdrop>
    </div>
  );
}