import { createTheme } from '@mui/material/styles';
import { orange, grey, red } from '@mui/material/colors';

export const darkTheme = createTheme({
    palette: {
        mode: 'dark',
        primary: {
            main: orange[200]
        }
    },
    breakpoints: {
        values: {
            xs: 600,
            sm: 800,
            md: 1000,
            lg: 1200,
            xl: 1536,
        },
    },
});

export const lightTheme = createTheme({
    palette: {
        mode: 'light',
        primary: {
            main: red[700],
        },
        secondary: {
            main: grey[300],
        },
    },
    breakpoints: {
        values: {
            xs: 600,
            sm: 800,
            md: 1000,
            lg: 1200,
            xl: 1536,
        },
    },
});