import { useEffect, useState } from 'react';
import { Stack, Checkbox, Container, Grid, Button, FormControlLabel, Grow,
         Card, CardActions, CardContent, CardMedia, Typography, Skeleton } from '@mui/material';
import FavoriteBorder from '@mui/icons-material/FavoriteBorder';
import Favorite from '@mui/icons-material/Favorite';
import FilterAltIcon from '@mui/icons-material/FilterAlt';

import axios from 'axios'
import { server_url } from './Secret'
import { member_list } from './stub';

const MyCard = (props) => {
    const card_size = 200

    const [heart, setHeart] = useState(false)
    const [imageLoadComplete, setImageLoadComplete] = useState(false)
    const [birthday, setBirthday] = useState('?')
    const [debut, setDebut] = useState('?')
    useEffect(()=>{
        const debut_ = new Date(Date.parse(props.member.debut))
        setDebut((debut_.getFullYear())+"/"+(debut_.getMonth()+1)+"/"+(debut_.getDate()))
        const birth_ = new Date(Date.parse(props.member.birthday))
        setBirthday((birth_.getMonth()+1)+"/"+(birth_.getDate()))
    }, [props.member.debut, props.member.birthday])

    const pressHeart = () => {
        setHeart(!heart)
    }

    return (
        <Card sx={{ minWidth: card_size }}>
            <Stack direction="row" justifyContent="flex-end" alignItems="center" spacing={1}>
                <Checkbox icon={<FavoriteBorder />} checkedIcon={<Favorite />} checked={heart} onClick={pressHeart}/>
            </Stack>
            
            <CardMedia component="img"
                       image={props.member.image}
                       onLoad={() => {setImageLoadComplete(true)}}
                       sx={{display:!imageLoadComplete?'none':'block', minWidth:card_size, minHeight:card_size}}/>
            {!imageLoadComplete &&
            <Skeleton animation="wave" variant="rectangle" sx={{minWidth:card_size, minHeight:card_size, paddingBottom:'100%'}}/>}
            
            <CardContent>
                <Typography gutterBottom variant="h6" component="div">
                    {imageLoadComplete===true?props.member.name:<Skeleton animation="wave" width={'80%'}/>}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                    {imageLoadComplete===true?"debut : "+debut:<Skeleton animation="wave" width={'50%'}/>}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                    {imageLoadComplete===true?"birthday : "+birthday:<Skeleton animation="wave" width={'50%'}/>}
                </Typography>
            </CardContent>
            <CardActions style={{justifyContent: 'center'}}>
                <Stack direction="column" justifyContent="flex-start" alignItems="center" spacing={1} marginBottom={2}>
                    <Button size="small" variant="contained"
                            href={props.member.official_profile} underline="none">
                            Official Profile
                    </Button>
                    <Button size="small" variant="outlined"
                            href={props.member.youtube} underline="none">
                            Youtube
                    </Button>
                    <Button size="small" variant="outlined"
                            href={props.member.twitter} underline="none">
                            Twitter
                    </Button>
                </Stack>
            </CardActions>
        </Card>
    )
}

export const Member = ( ) => {
    const [check, setCheck] = useState([true, true, true])
    const checkboxId = ['checkbox_jp', 'checkbox_en', 'checkbox_id']
    const handleCheckbox = (event) => {
        let check_status = check.slice()
        checkboxId.forEach((id, index)=>{if(id===event.target.id) check_status[index] = !check_status[index]})
        setCheck(check_status)
    }

    const [memberList, setMemberList] = useState([{},{},{}])
    useEffect(()=>{
        getMemberList()
    }, [])
    const getMemberList = async() => {
        await axios.get(server_url+'/memberList')
        .then((res)=>{
            res.data.forEach((member, n)=>{
                res.data[n].image = server_url+'/img/member_img/'+member.image
            })
            setMemberList(res.data)
        })
        .catch((err)=>{
            console.log(err)
            setMemberList(member_list)
        })
    }

    return (
        <Container maxWidth={'100%'}>
            <Stack direction="row" justifyContent="flex-end" alignItems="center" spacing={2}>
                <FilterAltIcon/>
                <FormControlLabel label='1' control={<Checkbox checked={check[0]} id={checkboxId[0]} onChange={handleCheckbox}/>}/>
                <FormControlLabel label='2' control={<Checkbox checked={check[1]} id={checkboxId[1]} onChange={handleCheckbox}/>}/>
                <FormControlLabel label='3' control={<Checkbox checked={check[2]} id={checkboxId[2]} onChange={handleCheckbox}/>}/>
            </Stack>
            <Grid container justifyContent="flex-start" alignItems="flex-start"
                  columns={{ xs: 2, sm: 3, md: 4, lg: 5, xl:6 }} spacing={3} marginBottom={3}>
                {memberList.map((member, index)=>{
                    return (
                    <Grow key={'GrowKey_'+index.toString()} in={true} timeout={{enter:500+index*50}}>
                        <Grid item xs={1}>
                            <MyCard member={member}/>
                        </Grid> 
                    </Grow>
                )})}
            </Grid>
        </Container>
    )
}